import { IonAlert, IonSpinner } from '@ionic/react'
import Uppy from '@uppy/core'
import { DashboardModal } from '@uppy/react'
import Webcam from '@uppy/webcam'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { AdminItemDetail, PageApp } from '../../components'
import { RUTAS_APP_FABRICANTE, TIPOS_REFRIGERACION_SELECT } from '../../constants'
import { useOpenPage, useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { ImagenProducto, Producto } from '../../services/api'

const ImageEditor = require('@uppy/image-editor')
const Espanol = require('@uppy/locales/lib/es_ES')
const XHRUpload = require('@uppy/xhr-upload')
const ImageCompressor = require('uppy-plugin-image-compressor')

export const FabricanteProductoDetalle: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Producto, setProducto] = useState(null as any)
  const [Imagenes, setImagenes] = useState<ImagenProducto[]>([])
  const [ImagenSeleccionada, setImagenSeleccionada] = useState<ImagenProducto>()
  const ProductoId = useRef<number>(parseInt(history.location.pathname.split('/')[3]))
  const [Loading, setLoading] = useState<boolean>(false)
  const [Alert, setAlert] = useState(false)
  const [VisibleUppy, setVisibleUppy] = useState(false)
  const [TituloAlert, setTituloAlert] = useState('')
  const [TextoAlert, setTextoAlert] = useState('')
  const [FuncionAlert, setFuncionAlert] = useState('')
  const addToast = useToaster()
  const openPage = useOpenPage()

  const setup = async () => {
    setLoading(true)
    obtenerProducto()
  }

  useEffect(() => {
    setup()
  }, [])

  const obtenerProducto = () => {
    ProductoId.current = parseInt(history.location.pathname.split('/')[3])
    rootStore.environment.api.getProductoAdmin(ProductoId.current).then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setProducto(result.data)
        setImagenes(result.data.imagenes)
      } else {
        addToast('Ocurri un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const confirmEliminarImagenProducto = () => {
    setFuncionAlert('confirmar')
    setTituloAlert('Atencin')
    setTextoAlert('Confirma que desea eliminar la imagen del producto?')
    setTimeout(() => {
      setAlert(true)
    }, 500)
  }

  const eliminarImagenProducto = (imagen_id: number) => {
    rootStore.environment.api.eliminarImagen(imagen_id).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Imagen eliminada satisfactoriamente', 'success')
        const imagenesProducto = [...Imagenes]
        const imagenEliminar = imagenesProducto.find((imagen) => imagen.id === imagen_id)
        if (imagenEliminar) {
          imagenesProducto.splice(imagenesProducto.indexOf(imagenEliminar), 1)
          setImagenes(imagenesProducto)
        }
      } else {
        addToast('Ocurri un error al eliminar vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const seleccionarImagenPortada = (imagen_id: number) => {
    rootStore.environment.api.seleccionarImagenPortada(imagen_id).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Imagen de portada seleccionada satisfactoriamente', 'success')
        const imagenes = [...Imagenes]
        const imagenesProducto = imagenes.map((img: any) => {
          return { ...img, portada: false }
        })
        const imagenPortada = imagenesProducto.find((imagen) => imagen.id === imagen_id)
        const imagenPortadaSeleccionada = { ...imagenPortada, portada: true }
        if (imagenPortada) {
          imagenesProducto.splice(imagenesProducto.indexOf(imagenPortada), 1)
          imagenesProducto.unshift(imagenPortadaSeleccionada)
          setImagenes(imagenesProducto)
        }
      } else {
        addToast('Ocurri un error al seleccionar la imagen de portada vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const botonesAlert = (accion: string) => {
    if (accion === 'imagen') {
      if (ImagenSeleccionada === undefined) {
        return
      }
      if (ImagenSeleccionada.portada) {
        return [
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Eliminar',
            handler: () => confirmEliminarImagenProducto(),
          },
        ]
      } else {
        return [
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Imagen de portada',
            handler: () => seleccionarImagenPortada(ImagenSeleccionada.id),
          },
          {
            text: 'Eliminar',
            handler: () => {
              setAlert(false)
              confirmEliminarImagenProducto()
            },
          },
        ]
      }
    } else if (accion === 'eliminar') {
      return [
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
        },
        {
          text: 'Confirmar',
          handler: () => eliminar(),
        },
      ]
    } else if (accion === 'nueva-imagen') {
      return [
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
        },
        {
          text: 'Confirmar',
          handler: () => setVisibleUppy(!VisibleUppy),
        },
      ]
    } else {
      if (ImagenSeleccionada === undefined) {
        return
      }
      return [
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
        },
        {
          text: 'Confirmar',
          handler: () => eliminarImagenProducto(ImagenSeleccionada.id),
        },
      ]
    }
  }

  const uppy = React.useMemo(() => {
    return Uppy({
      restrictions: { allowedFileTypes: ['image/*'] },
      meta: {
        producto: ProductoId.current,
      },
      locale: Espanol,
    })
      .use(Webcam, {
        videoConstraints: {
          facingMode: 'environment',
        },
      })
      .use(XHRUpload, {
        endpoint: rootStore.environment.api.config.url + '/imagen_producto/',
        headers: {
          Authorization: 'Token ' + loginStore.token,
        },
        method: 'POST',
        formData: true,
        fieldName: 'imagen',
      })
      .use(ImageEditor, {
        id: 'ImageEditor',
        quality: 0.8,
        cropperOptions: {
          viewMode: 1,
          background: false,
          autoCropArea: 1,
          responsive: true,
        },
      })
      .use(ImageCompressor, {
        maxHeight: 2000,
        maxWidth: 2000,
        quality: 0.8,
      })
  }, [ProductoId.current])

  React.useEffect(() => {
    return () => uppy.close()
  }, [])

  uppy.on('complete', (result) => {
    const imagenesSubidas: { id: any; imagen: any }[] = []
    for (let i = 0; i < result.successful.length; i++) {
      // @ts-ignore
      let data = result.successful[i].response.body
      let imagen = {
        // @ts-ignore
        id: data.id,
        // @ts-ignore
        imagen: data.imagen,
        // @ts-ignore
        producto: data.producto,
      }
      if (!imagenesSubidas.includes(imagen)) {
        imagenesSubidas.push(imagen)
      }
    }
    let imagenesUpdate: any[] = []
    imagenesUpdate = imagenesUpdate.concat(Imagenes)
    imagenesUpdate = imagenesUpdate.concat(imagenesSubidas)
    setImagenes(imagenesUpdate)
  })

  const eliminar = () => {
    rootStore.environment.api.eliminarProducto(ProductoId.current).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Producto eliminado exitosamente', 'success')
        history.replace(RUTAS_APP_FABRICANTE.CATALOGO)
      } else {
        addToast('Ocurri un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  return (
    <PageApp
      pageStyles={'background-blanco'}
      accion_boton_derecho={() => history.goBack()}
      boton_derecho={<FIcon.X size={30} color={'white'} />}
      titulo_header={Producto ? Producto.nombre : 'Producto'}
      sub_titulo={` `}
      mostrar_barra={false}
    >
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TituloAlert}
        message={TextoAlert}
        buttons={botonesAlert(FuncionAlert)}
      />

      <DashboardModal
        uppy={uppy}
        closeModalOnClickOutside
        open={VisibleUppy}
        onRequestClose={() => {
          setVisibleUppy(false)
          uppy.reset()
        }}
        plugins={['Webcam', 'ImageEditor']}
        metaFields={[{ id: 'name', name: 'Nombre', placeholder: 'Nombre del archivo' }]}
      />
      {Loading && <IonSpinner name="crescent" className={'loadingSpinnerFabricanteApp pt-5'} />}

      {!Loading && Producto && (
        <div className={'d-flex flex-column h-100 overflow-auto py-2'}>
          <div className={'row mx-1'}>
            <AdminItemDetail titulo={'Nombre'} texto={Producto.nombre} columnas={12} />
            <AdminItemDetail titulo={'Descripcin'} texto={Producto.descripcion} columnas={12} />
            <AdminItemDetail titulo={'Descripcin extensa'} html={Producto.descripcion_extensa} columnas={12} />
            <AdminItemDetail titulo={'Etiquetas'} texto={Producto.etiquetas} columnas={12} />
            <AdminItemDetail titulo={'Categora'} texto={Producto.categoria.nombre} columnas={12} />
            <AdminItemDetail
              titulo={'Refrigeracin'}
              texto={
                TIPOS_REFRIGERACION_SELECT.filter((val) => parseInt(val.value) === Producto.refrigeracion)[0].label
              }
              columnas={12}
            />
            <AdminItemDetail
              titulo={'Estado'}
              texto={Producto.activo ? 'Activo' : 'Pendiente de moderacin'}
              columnas={3}
            />
            <AdminItemDetail
              titulo={'Cantidad mayorista'}
              separador_miles
              texto={Producto.cantidad_mayorista}
              columnas={12}
            />
            <AdminItemDetail titulo={'Precio'} separador_dinero texto={Producto.precio} columnas={12} />
            <AdminItemDetail
              titulo={'Precio mejorado'}
              separador_dinero
              texto={Producto.precio_mayorista}
              columnas={12}
            />
            <AdminItemDetail
              titulo={'Cantidad disponible'}
              separador_miles
              texto={Producto.cantidad_disponible}
              columnas={12}
            />
            <AdminItemDetail titulo={'Peso kg'} texto={Producto.peso_kg} columnas={12} />
            <AdminItemDetail titulo={'Altura cm'} texto={Producto.alto_cm} columnas={12} />
            <AdminItemDetail titulo={'Ancho cm'} texto={Producto.ancho_cm} columnas={12} />
            <AdminItemDetail titulo={'Profundidad cm'} texto={Producto.profundidad_cm} columnas={12} />
            <div className={'col-12 col-lg-3'}>
              <strong>Video</strong>
              {!Producto.video && <p>No posee video</p>}
              {Producto.video && <video src={Producto.video} controls style={{width: 640, height: 360, maxWidth: '100%'}} />}
            </div>
            <p className={'px-3 text-gray'}>
              <strong>Imgenes del producto</strong>
            </p>
            {Imagenes.length < 1 && <div className={'col-12'}><p>No posee imgenes</p></div>}
            {Imagenes.map((img) => {
              return (
                <div
                  className={'col-6'}
                  key={img.id + ''}
                  onClick={() => {
                    setImagenSeleccionada(img)
                    setTituloAlert('Acciones')
                    setTextoAlert('')
                    setFuncionAlert('imagen')
                    setAlert(true)
                  }}
                >
                  <p className={'d-flex justify-content-center'}>
                    <img alt={img.id + ' '} src={img.imagen} className={'max-min-img-admin'} />
                  </p>
                </div>
              )
            })}
            <div
              className={'btn btn-outline-primary btn-block mx-5 mt-3'}
              onClick={openPage(RUTAS_APP_FABRICANTE.PRODUCTO_UPDATE(ProductoId.current))}
            >
              Editar producto
            </div>
            <div
              className={'btn btn-outline-primary btn-block mx-5 mt-3'}
              onClick={() => {
                setTituloAlert('Atencin')
                setTextoAlert(
                  'Al agregar una nueva imagen el producto pasar a estar pendiente de aprobacin por parte del staff de ABi',
                )
                setFuncionAlert('nueva-imagen')
                setAlert(true)
              }}
            >
              Agregar imgenes
            </div>
            <div
              className={'btn btn-outline-primary btn-block mx-5 mt-3'}
              onClick={() => {
                setTituloAlert('Eliminar')
                setTextoAlert('Confirma que desea eliminar el producto?')
                setFuncionAlert('eliminar')
                setAlert(true)
              }}
            >
              Eliminar producto
            </div>
          </div>
        </div>
      )}
    </PageApp>
  )
})

export default FabricanteProductoDetalle
